<template><section>
<h2>imRez图片压缩</h2>
<pre v-pre=""><code class="hljs language-javascript">  <span class="hljs-keyword">import</span> { ImRez } <span class="hljs-keyword">from</span> <span class="hljs-string">'dby-base'</span>
  Vue.use(ImRez)
  <span class="hljs-comment">// 访问 this.$imRez</span>
</code></pre>
<h4>upladFile 上传图片文件</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;input type=\&quot;file\&quot; name=\&quot;image\&quot; accept=\&quot;image/*\&quot; @change=\&quot;onChangeUpladFile\&quot;&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  /**\n   * @param\n   * file 文件流\n   * url 上传地址\n   * data 上传带参\n   * method 请求方式\n   * size 默认1m超出这个限制就压缩\n   * succ 成功回调\n   * fail失败回调\n   * quality 压缩比例 默认0.7\n   */\n  methods: {\n    onChangeUpladFile (e) {\n      let files = e.target.files;\n      for(let file of files){\n        this.$imRez.upladFile({file, url: this.$env.apiH5 + '/a', data: {}, method: 'POST', succ(){}, fail(){}})\n      } \n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <input type="file" name="image" accept="image/*" @change="onChangeUpladFile">
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"file"</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"image"</span>  <span class="hljs-attr">accept</span>=<span class="hljs-string">"image/*"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onChangeUpladFile"</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-comment">/**
   * @param
   * file 文件流
   * url 上传地址
   * data 上传带参
   * method 请求方式
   * size 默认1m超出这个限制就压缩
   * succ 成功回调
   * fail失败回调
   * quality 压缩比例 默认0.7
   */</span>
  methods: {
    onChangeUpladFile (e) {
      <span class="hljs-keyword">let</span> files = e.target.files;
      <span class="hljs-keyword">for</span>(<span class="hljs-keyword">let</span> file <span class="hljs-keyword">of</span> files){
        <span class="hljs-keyword">this</span>.$imRez.upladFile({file, <span class="hljs-attr">url</span>: <span class="hljs-keyword">this</span>.$env.apiH5 + <span class="hljs-string">'/a'</span>, <span class="hljs-attr">data</span>: {}, <span class="hljs-attr">method</span>: <span class="hljs-string">'POST'</span>, succ(){}, fail(){}})
      } 
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h4>photoCompress 直接压缩返回base64</h4>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;div&gt;\n    &lt;input type=\&quot;file\&quot; name=\&quot;image\&quot; accept=\&quot;image/*\&quot; @change=\&quot;onChangePhotoCompress\&quot;&gt;\n    &lt;img src ref=\&quot;img\&quot; style=\&quot;max-width: 500px;\&quot;&gt;\n  &lt;/div&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  methods: {\n    onChangePhotoCompress(e) {\n      let files = e.target.files;\n      for(let file of files){\n        /**\n         * @param\n         * file 文件流\n         * quality 压缩比例\n         * base64\n         */\n        this.$imRez.photoCompress(\n          file,\n          {\n            quality: 0.1\n          },\n          base64Codes =&gt; {\n            this.$refs.img.src = base64Codes\n          }\n        )\n      } \n    }\n  }\n}\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <div>
    <input type="file" name="image" accept="image/*" @change="onChangePhotoCompress">
    <img src="" ref="img" style="max-width: 500px;">
  </div>
</template>

</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">input</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"file"</span> <span class="hljs-attr">name</span>=<span class="hljs-string">"image"</span>  <span class="hljs-attr">accept</span>=<span class="hljs-string">"image/*"</span> <span class="hljs-attr">@change</span>=<span class="hljs-string">"onChangePhotoCompress"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">img</span> <span class="hljs-attr">src</span>=<span class="hljs-string">""</span>  <span class="hljs-attr">ref</span>=<span class="hljs-string">"img"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"max-width: 500px;"</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  <span class="hljs-attr">methods</span>: {
    onChangePhotoCompress(e) {
      <span class="hljs-keyword">let</span> files = e.target.files;
      <span class="hljs-keyword">for</span>(<span class="hljs-keyword">let</span> file <span class="hljs-keyword">of</span> files){
        <span class="hljs-comment">/**
         * @param
         * file 文件流
         * quality 压缩比例
         * base64
         */</span>
        <span class="hljs-keyword">this</span>.$imRez.photoCompress(
          file,
          {
            <span class="hljs-attr">quality</span>: <span class="hljs-number">0.1</span>
          },
          base64Codes =&gt; {
            <span class="hljs-keyword">this</span>.$refs.img.src = base64Codes
          }
        )
      } 
    }
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
export default {
  methods: {
    onChangeUpladFile (e) {
      let files = e.target.files;
      for(let file of files){
        this.$imRez.upladFile({file, url: this.$env.apiH5 + '/a', data: {'c':'11'}, method: 'POST', succ(){}, fail(){}})
      } 
    },
    onChangePhotoCompress(e) {
      let files = e.target.files;
      for(let file of files){
        this.$imRez.photoCompress(
          file,
          {
            quality: 0.1
          },
          base64Codes => {
            this.$refs.img.src = base64Codes
          }
        )
      } 
    }
  }
}
</script>


